const address = '0x8F4d66009AB4f3a02f33A96e0B4e3b87d1033173'; // insert your contract's address here

const provider = new ethers.providers.JsonRpcProvider('https://cloudflare-eth.com/');
const contract = new ethers.Contract(address, abi, provider);

function App() {
  const [to, setTo] = useState('');
  const [signature, setSignature] = useState('');

  const handleMintButtonClick = () => {
    contract.submitMint(to, signature)
      .then(() => {
        console.log('Mint transaction submitted successfully');
        // Add any additional logic or UI updates you need here
      })
      .catch((error) => {
        console.error('Error submitting mint transaction:', error);
        // Add any error handling or UI updates you need here
      });
  };

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
      <main>
        <label htmlFor="toInput">To:</label>
        <input
          type="text"
          id="toInput"
          value={to}
          onChange={(event) => setTo(event.target.value)}
        />

        <label htmlFor="signatureInput">Signature:</label>
        <textarea
          id="signatureInput"
          value={signature}
          onChange={(event) => setSignature(event.target.value)}
        ></textarea>

        <button onClick={handleMintButtonClick}>Submit Mint Transaction</button>
      </main>
    </div>
  );
}

export default App;
